/* CSS RESET */
* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}


/* GLOBAL STYLES */
body {
    background-color: #111518;
    font-family: Arial, Helvetica, sans-serif;
}

.container {
    max-width: 1200px;
    margin: 0 auto 50px auto;
}


/* HEADER STYLES */
header {
    /* background-color: cadetblue; */
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 80px;
}

.logo {
    font-size: 30px;
    font-weight: 200;
    color: #ffff;
}

.nav__list {
    height: 100%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    margin: 0 auto;
}

.nav__item {
    color: #ffff; 
    list-style: none;
    font-size: 14px;
}


/* WELCOME SECTION */
.welcome__section {

    background-color: royalblue;
    height: 700px;
    border-radius: 10px;
    display: flex;
    justify-content: space-evenly;
    padding: 5px;
    color: #fff;
    margin-top: 80px;
}

.welcome__title--container {
    width: 33%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}

.welcome__title {
    font-size: 70px;
    text-transform: uppercase;
}


.welcome__poster--container {
    height: 100%;
    width: 33%;
}

.welcome__poster {
    height: 600px;
    width: 100%;
    border-radius: 4px;
}

.description__container {
    width: 33%;
}

.welcome__story--description {
    margin-top: 20px;
}

/* SEARCH INPUT CONTAINER STYLES */
.search__input--container {
    display: flex;
    justify-content: center;
}

.search__bar {
    background: #273c75;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-radius: 50px;
    width: 300px;
    height: 35px;
}

.search__input {
    width: 80%;
    margin-left: 15px;
    border: none;
    height: 80%;
    background: none;
    outline: none;
    color: #fff;
}

.search__input::placeholder {
    color: #ffff;
}

.search {
    height: 75%;
    cursor: pointer;
    border: none;
    padding: 0 8px;
    border-radius: 50px;
    outline: none;
    margin-right: 5px
}

/* UPCOMING MOVIES */
.upcoming {
    margin-top: 100px;
    /* border: 3px solid white; */
    height: auto;
}

/* .upcoming__movies {
    display: flex;
    height: 500px;
} */

.upcoming__card--wrapper {
    display: flex;
    margin-right: 20px;
}

.upcoming__poster {
    cursor: pointer;
    width: 350px;
    height: 500px;
}

.upcoming__title {
    font-size: 60px;
    font-weight: 200;
    color: #fff;
}

.upcoming__description {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
}

.upcoming__text--container {
    width: 80%;
    color: #fff;
    opacity: .7;
}

/* .upcoming__poster:hover {
    transition: all .3s ease;
    opacity: .4;
} */

/* TOP RATED MOVIES */
.top__rated {
    margin-top: 100px;
    height: auto;
}

.top__rated--movies {
    display: flex;
}

.top__rated--wrapper {
    display: flex;
    flex-direction: column;
    width: 280px;
    height: auto;
    margin-top: 20px;
    margin-right: 40px;
}

.top__rated--heading {
    margin-left: 10px;
    color: #fff;
}

.top__rated--poster {
    cursor: pointer;
    width: 300px;
    height: 490px;
}


/* SEARCH OUTPUT SECTION */
.search__results {
    margin-top: 80px;
    display: flex;
    flex-direction: column;
}

.search__results--heading {
    text-align: center;
    font-weight: 200;
    font-size: 30px;
    color: #fff;
}

.output {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
}

.template__wrapper {
    width: 240px;
    margin: 40px 0;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.search__movie--poster {
    height: 300px;
    width: auto;
    border-radius: 4px;
    cursor: pointer;
}

.search__movie--poster:hover {
    transition: all .3s ease;
    opacity: .4;
}

.movie__title {
    padding: 10px;
    color: rgba(255, 255, 255, 0.746);
    font-weight: 200;
    font-size: 14px;
    text-align: center;
}

.more__details {
    margin-top: 10px;
    padding: 8px 10px;
    cursor: pointer;
    background: rgba(252, 7, 7, 0.797);
    color: #fff;
    border: none;
    border-radius: 4px;
}

/* CLASSLISTS */
.hideAll {
    display: none;
}